@import "./varaibles";

.button {
    display: inline-block;
    border-radius: 0px;
    text-decoration: none;
    text-align: center;
    appearance: none;
    cursor: pointer;
    background-color: transparent;
    border: none;
    position: relative;
    outline: none;
    overflow: hidden;
    &_gradient-border {
        color: $text-color-accent;
        border: 3px solid $text-color-accent;
        border-image: linear-gradient(to right, $text-color-accent, $orange) 1 40%; }
    &_big, &_small {
        text-transform: uppercase;
        font-size: 21px;
        font-weight: 900;
        width: 100%;
        padding: 27px; }
    &_big {
        max-width: 470px; }
    &_small {
        max-width: 370px; }
    &_bg {
        color: #ffffff;
        background-image: url(../img/submit-btn-bg.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center; } }

@media screen and ( max-width: 768px ) {
    .button {
        &_big, &_small {
            padding: 21px; } } }
