.chat {
  display: flex;
  align-items: stretch;
  position: relative;
}

.chat__dialogs {
  flex-shrink: 0;
  max-width: 295px;
  width: 100%;
  padding-right: 30px;
  max-height: 510px;
  overflow-y: auto;
  list-style-type: none;
  border-right: 1px solid #898989;
}

.chat__dialog {
  height: 100%;
}

.chat__dialog-item {
  width: 100%;
}

.chat__dialog-link {
  width: 100%;
  display: inline-block;
  padding: 20px 30px;
  font-size: 21px;
  color: #898989;
  transition: all 0.3s ease-out 0s;
}

.chat__dialog-link:hover, .chat__dialog-link.active {
  background-color: #282828;
  font-weight: 700;
  color: #ffffff;
}

.chat__trigger {
  display: none;
  position: absolute;
  z-index: 4;
  left: 0;
  top: 0;
  height: 35px;
  width: 35px;
  border: 1px solid #fff;
  border-radius: 3px;
  transition: background-color 0.3s ease-out 0s;
  transform: translateY(-120%);
}

.chat__trigger svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
  transition: fill 0.3s ease-out 0s;
}

.chat__trigger:hover, .chat__trigger.active {
  background-color: #fff;
}

.chat__trigger:hover svg, .chat__trigger.active svg {
  fill: #000000;
}

@media screen and (max-width: 920px) {
  .chat__trigger {
    display: inline-block;
  }
  .chat__dialogs {
    padding: 0px 10px;
    border: none;
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    max-height: 590px;
    max-width: none;
    width: 100%;
    display: block;
    background-color: rgba(0, 0, 0, 0.9);
    transform: translateX(-100%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s ease-out 0s;
  }
  .chat__dialogs.active {
    transform: translateX(0%);
    opacity: 1;
    pointer-events: all;
  }
  .chat__dialog-link {
    padding: 15px 20px;
  }
}
