@import "./varaibles";

.program {
    padding-bottom: 260px;
    &__title {
        width: 100%;
        text-align: center;
        display: block;
        font-weight: 700;
        font-size: 24px;
        text-transform: uppercase;
        margin-bottom: 50px; }
    &__table {
        width: 100%; }
    &__tables {
        margin-bottom: 200px; }
    &__time + &__name, &__time + &__content {
        margin-left: 50px; }
    &__time {
        font-size: 18px;
        font-weight: 700;
        flex-shrink: 0;
        min-width: 50px;
        display: inline-block;
        color: inherit; }
    &__name {
        font-size: 18px;
        font-weight: 500;
        color: inherit;
        text-decoration: underline;
        text-transform: uppercase; }
    &__schedule {
        &-item {
            display: flex;
            align-items: flex-start;
            width: 100%;
            padding: 30px;
            border: {
                left: 2px solid transparent;
                right: 2px solid transparent;
                top: 2px solid #252525;
                bottom: 2px solid #252525; }
            color: $text-color-grey;
            &_center {
                &:first-child {
                    border-top: none;
                    padding-top: 0; }
                .program__time + .program__name {
                    margin-left: 20px !important; }
                .program {
                    &__name, &__time {
                        color: #ffffff;
                        text-decoration: none;
                        font-size: 21px; }
                    &__name {
                        display: inline-block; } } }
            &.disabled {
                color: #545454;
                .program {
                    &__name {
                        text-decoration: none; } } }
            &.active {
                color: $text-color-accent;
                border-color: $text-color-accent;
                border-image: linear-gradient(to right, $text-color-accent, $orange) 1 40%; } } }
    &__content {
        &-title, &-text {
            font-size: 18px; }
        &-title {
            color: #ffffff;
            &_ttu {
                text-transform: uppercase; }
            &_block {
                display: block; } }
        &-text {
            color: $text-color-grey; }
        ul {
            list-style-type: none; }
        li {
            color: inherit; } }
    &__ordered-list {
        margin-left: 20px;
        li {
            font-size: 18px;
            color: $text-color-grey;
            font-weight: 500;
            text-transform: uppercase;
            padding-left: 50px; }
        li + li {
            margin-top: 20px; } } }

@media screen and ( min-width: 1040px ) {
    .program {
        &__schedule {
            &-item {
                &_center {
                    .program__time + .program__name {
                        margin-left: 20px !important; }
                    .program {
                        &__name, &__time {
                            color: #ffffff;
                            font-size: 24px;
                            font-weight: 700; }
                        &__name {
                            text-align: center;
                            width: calc( 100% - 140px ); } } } } } } }

@media screen and ( max-width: 920px ) {
    .program {
        padding-bottom: 100px; } }

@media screen and ( max-width: 520px ) {
    .program {
        padding-bottom: 80px;
        &__time + &__name, &__time + &__content {
            margin-left: 0px;
            margin-top: 15px; }
        &__name {
            width: 100%;
            text-align: center;
            font-size: 16px; }
        &__time {
            font-size: 18px; }
        &__schedule {
            &-item {
                padding: 15px 10px;
                flex-direction: column;
                &_center {
                    .program__time + .program__name {
                        margin-left: 0 !important; }
                    .program {
                        &__name, &__time {
                            font-size: 18px; } } } } }
        &__ordered-list {
            li {
                padding-left: 0;
                font-size: 16px; } } } }

@media screen and ( max-width: 350px ) {
    .program {
        &__schedule {
            &-item {
                &_center {
                    .program__time + .program__name {
                        margin-left: 0 !important; }
                    .program {
                        &__name, &__time {
                            font-size: 16px; } } } } } } }
