.hero {
  padding-top: 170px;
  padding-bottom: 150px;
}

.hero__wrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.hero__body {
  flex-grow: 1;
}

.hero__title + .hero__date {
  margin-top: 125px;
}

.hero__title {
  max-width: 780px;
  font-weight: 700;
  font-size: 36px;
  line-height: 60px;
  text-transform: uppercase;
  position: relative;
}

.hero__title_colored {
  max-width: 880px;
  text-transform: initial;
  font-size: 54px;
  font-weight: 400;
  color: #ff0a36;
}

.hero__title::before {
  content: '';
  display: inline-block;
  width: 3px;
  position: absolute;
  background-color: #ffffff;
  left: -30px;
  bottom: 12px;
  height: 525px;
  transform-origin: top;
  transform: scaleY(1);
  animation: grow-bottom 2s 1 linear 0s;
}

.hero__deco {
  position: absolute;
  transition: transform 0.3s ease-out 0s;
  transition-duration: 0.05s;
}

.hero__deco_logo {
  max-width: 740px;
  right: 300px;
  top: -290px;
}

.hero__deco:not(.hero__deco_logo):nth-of-type(1) {
  height: 1125px;
  width: 1125px;
  left: -880px;
  top: -80px;
}

.hero__deco:not(.hero__deco_logo):nth-of-type(2) {
  height: 625px;
  width: 625px;
  right: 0px;
  top: 130px;
}

.hero__date {
  display: flex;
  align-items: center;
}

.hero__date-line {
  background-image: url(../img/text-bg.jpg);
  background-size: cover;
  background-position-y: 20px;
  max-width: 290px;
  width: 100%;
  height: 2px;
  background-color: #ff0a36;
  margin: 0px 10px;
}

.hero__date-text {
  white-space: nowrap;
  color: #ff0a36;
  font-size: 36px;
  font-weight: bold;
}

.hero__aside {
  max-width: 225px;
  padding-left: 30px;
  position: relative;
}

.hero__aside::before {
  content: '';
  display: inline-block;
  width: 4px;
  background-size: contain;
  background-image: url(../img/vertical-line-bg.png);
  background-repeat: repeat-y;
  position: absolute;
  background-color: transparent;
  left: 0px;
  top: 7px;
  height: 527px;
  transform-origin: top;
  transform: scaleY(1);
  animation: grow-bottom-height 2.6s 1 linear 0s;
}

.hero__aside-text {
  font-size: 36px;
  line-height: 40px;
}

.hero_ceremony {
  padding-bottom: 30px;
}

@supports (background-clip: text) {
  .hero__date, .hero__title_colored {
    background-image: url(../img/text-bg.jpg);
    background-position-x: 20px;
    background-size: cover;
    background-clip: text;
    color: transparent;
  }
  .hero__date-text, .hero__title_colored-text {
    color: transparent;
  }
}

@media screen and (max-width: 1600px) {
  .hero__deco:not(.hero__deco_logo):nth-of-type(1) {
    height: 820px;
    width: 820px;
    left: -620px;
    top: 20px;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    height: 440px;
    width: 440px;
  }
}

@media screen and (max-width: 1366px) {
  .hero__deco {
    opacity: .8;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(1) {
    height: 640px;
    width: 640px;
    left: -500px;
    top: 80px;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    height: 340px;
    width: 340px;
  }
}

@media screen and (max-width: 1310px) {
  .hero__deco:not(.hero__deco_logo):nth-of-type(1) {
    display: none;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    right: -50px;
    top: 90px;
  }
}

@media screen and (max-width: 1070px) {
  .hero {
    padding: 160px 0px 100px;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    height: 400px;
    width: 400px;
    right: -55px;
    top: 90px;
  }
  .hero__body {
    max-width: 730px;
  }
  .hero__title + .hero__date {
    margin-top: 80px;
  }
  .hero__title {
    font-size: 30px;
    line-height: 42px;
  }
  .hero__title_colored {
    line-height: 36px;
  }
  .hero__aside {
    display: none;
  }
}

@media screen and (max-width: 920px) {
  .hero {
    padding-top: 110px;
    padding-bottom: 90px;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    height: 350px;
    width: 350px;
    right: -90px;
    top: 70px;
  }
}

@media screen and (max-width: 720px) {
  .hero__date-text {
    font-size: 30px;
  }
}

@media screen and (max-width: 520px) {
  .hero {
    padding-top: 100px;
    padding-bottom: 50px;
  }
  .hero__deco:not(.hero__deco_logo):nth-of-type(2) {
    height: 190px;
    width: 190px;
    right: -70px;
    top: 70px;
  }
}

@media screen and (max-width: 450px) {
  .hero__title {
    font-size: 26px;
    line-height: 38px;
  }
  .hero__title + .hero__date {
    margin-top: 60px;
  }
  .hero__date-text {
    font-size: 24px;
  }
}

@media screen and (max-width: 370px) {
  .hero {
    padding-bottom: 60px;
  }
  .hero__title {
    font-size: 22px;
    line-height: 32px;
  }
  .hero__title_colored {
    line-height: 28px;
  }
  .hero__title + .hero__date {
    margin-top: 40px;
  }
}
