.logo {
  display: block;
  max-width: 170px;
  width: 100%;
  flex-shrink: 0;
  font-size: 17px;
  font-weight: 700;
}

.logo__img {
  width: 100%;
  height: auto;
}

.logo_powered {
  margin-left: auto;
}

.logo_footer {
  margin-bottom: 45px;
}

.logo_nav {
  margin-top: auto;
}

.logo + .burger {
  margin-left: 20px;
}

@media screen and (max-width: 1140px) {
  .logo_powered {
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .logo {
    max-width: 110px;
    font-size: 14px;
  }
  .logo_footer {
    margin-bottom: 0px;
  }
}
