@import "./varaibles";

.hero {
    padding-top: 170px;
    padding-bottom: 150px;
    &__wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-between; }
    &__body {
        flex-grow: 1; }
    &__title + &__date {
        margin-top: 125px; }
    &__title {
        max-width: 780px;
        font-weight: 700;
        font-size: 36px;
        line-height: 60px;
        text-transform: uppercase;
        position: relative;
        &_colored {
            max-width: 880px;
            text-transform: initial;
            font-size: 54px;
            font-weight: 400;
            color: $text-color-accent; }
        &::before {
            content: '';
            display: inline-block;
            width: 3px;
            position: absolute;
            background-color: $text-color;
            left: -30px;
            bottom: 12px;
            height: 525px;
            transform-origin: top;
            transform: scaleY(1);
            animation: grow-bottom 2s 1 linear 0s; } }
    &__deco {
        position: absolute;
        @include transition(transform);
        transition-duration: 0.05s;
        &_logo {
            max-width: 740px;
            right: 300px;
            top: -290px; }
        &:not(&_logo):nth-of-type(1) {
            height: 1125px;
            width: 1125px;
            left: -880px;
            top: -80px; }
        &:not(&_logo):nth-of-type(2) {
            height: 625px;
            width: 625px;
            right: 0px;
            top: 130px; } }
    &__date {
        display: flex;
        align-items: center;
        &-line {
            background-image: url(../img/text-bg.jpg);
            background-size: cover;
            background-position-y: 20px;
            max-width: 290px;
            width: 100%;
            height: 2px;
            background-color: $text-color-accent;
            margin: 0px 10px; }
        &-text {
            white-space: nowrap;
            color: $text-color-accent;
            font-size: 36px;
            font-weight: bold; } }
    &__aside {
        max-width: 225px;
        padding-left: 30px;
        position: relative;
        &::before {
            content: '';
            display: inline-block;
            width: 4px;
            background-size: contain;
            background-image: url(../img/vertical-line-bg.png);
            background-repeat: repeat-y;
            position: absolute;
            background-color: transparent;
            left: 0px;
            top: 7px;
            height: 527px;
            transform-origin: top;
            transform: scaleY(1);
            animation: grow-bottom-height 2.6s 1 linear 0s; }
        &-text {
            font-size: 36px;
            line-height: 40px; } }
    &_ceremony {
        padding-bottom: 30px; } }

@supports (background-clip: text) {
    .hero {
        &__date, &__title_colored {
            background-image: url(../img/text-bg.jpg);
            background-position-x: 20px;
            background-size: cover;
            background-clip: text;
            color: transparent;
            &-text {
                color: transparent; } } } }

@media screen and ( max-width: 1600px ) {
    .hero {
        &__deco {
            &:not(&_logo):nth-of-type(1) {
                height: 820px;
                width: 820px;
                left: -620px;
                top: 20px; }
            &:not(&_logo):nth-of-type(2) {
                height: 440px;
                width: 440px; } } } }

@media screen and ( max-width: 1366px ) {
    .hero {
        &__deco {
            opacity: .8;
            &:not(&_logo):nth-of-type(1) {
                height: 640px;
                width: 640px;
                left: -500px;
                top: 80px; }
            &:not(&_logo):nth-of-type(2) {
                height: 340px;
                width: 340px; } } } }

@media screen and ( max-width: 1310px ) {
    .hero {
        &__deco {
            &:not(&_logo):nth-of-type(1) {
                display: none; }
            &:not(&_logo):nth-of-type(2) {
                right: -50px;
                top: 90px; } } } }

@media screen and ( max-width: 1070px ) {
    .hero {
        padding: 160px 0px 100px;
        &__deco {
            &:not(&_logo):nth-of-type(2) {
                height: 400px;
                width: 400px;
                right: -55px;
                top: 90px; } }
        &__body {
            max-width: 730px; }
        &__title + &__date {
            margin-top: 80px; }
        &__title {
            font-size: 30px;
            line-height: 42px;
            &_colored {
                line-height: 36px; } }
        &__aside {
            display: none; } } }

@media screen and ( max-width: 920px ) {
    .hero {
        padding-top: 110px;
        padding-bottom: 90px;
        &__deco {
            &:not(&_logo):nth-of-type(2) {
                height: 350px;
                width: 350px;
                right: -90px;
                top: 70px; } } } }

@media screen and ( max-width: 720px ) {
    .hero {
        &__date {
            &-text {
                font-size: 30px; } } } }

@media screen and ( max-width: 520px ) {
    .hero {
        padding-top: 100px;
        padding-bottom: 50px;
        &__deco {
            &:not(&_logo):nth-of-type(2) {
                height: 190px;
                width: 190px;
                right: -70px;
                top: 70px; } } } }

@media screen and ( max-width: 450px ) {
    .hero {
        &__title {
            font-size: 26px;
            line-height: 38px; }
        &__title + &__date {
            margin-top: 60px; }
        &__date {
            &-text {
                font-size: 24px; } } } }

@media screen and ( max-width: 370px ) {
    .hero {
        padding-bottom: 60px;
        &__title {
            font-size: 22px;
            line-height: 32px;
            &_colored {
                line-height: 28px; } }
        &__title + &__date {
            margin-top: 40px; } } }
