.program {
  padding-bottom: 260px;
}

.program__title {
  width: 100%;
  text-align: center;
  display: block;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.program__table {
  width: 100%;
}

.program__tables {
  margin-bottom: 200px;
}

.program__time + .program__name, .program__time + .program__content {
  margin-left: 50px;
}

.program__time {
  font-size: 18px;
  font-weight: 700;
  flex-shrink: 0;
  min-width: 50px;
  display: inline-block;
  color: inherit;
}

.program__name {
  font-size: 18px;
  font-weight: 500;
  color: inherit;
  text-decoration: underline;
  text-transform: uppercase;
}

.program__schedule-item {
  display: flex;
  align-items: flex-start;
  width: 100%;
  padding: 30px;
  border-left: 2px solid transparent;
  border-right: 2px solid transparent;
  border-top: 2px solid #252525;
  border-bottom: 2px solid #252525;
  color: #898989;
}

.program__schedule-item_center:first-child {
  border-top: none;
  padding-top: 0;
}

.program__schedule-item_center .program__time + .program__name {
  margin-left: 20px !important;
}

.program__schedule-item_center .program__name, .program__schedule-item_center .program__time {
  color: #ffffff;
  text-decoration: none;
  font-size: 21px;
}

.program__schedule-item_center .program__name {
  display: inline-block;
}

.program__schedule-item.disabled {
  color: #545454;
}

.program__schedule-item.disabled .program__name {
  text-decoration: none;
}

.program__schedule-item.active {
  color: #ff0a36;
  border-color: #ff0a36;
  border-image: linear-gradient(to right, #ff0a36, #ff6b3a) 1 40%;
}

.program__content-title, .program__content-text {
  font-size: 18px;
}

.program__content-title {
  color: #ffffff;
}

.program__content-title_ttu {
  text-transform: uppercase;
}

.program__content-title_block {
  display: block;
}

.program__content-text {
  color: #898989;
}

.program__content ul {
  list-style-type: none;
}

.program__content li {
  color: inherit;
}

.program__ordered-list {
  margin-left: 20px;
}

.program__ordered-list li {
  font-size: 18px;
  color: #898989;
  font-weight: 500;
  text-transform: uppercase;
  padding-left: 50px;
}

.program__ordered-list li + li {
  margin-top: 20px;
}

@media screen and (min-width: 1040px) {
  .program__schedule-item_center .program__time + .program__name {
    margin-left: 20px !important;
  }
  .program__schedule-item_center .program__name, .program__schedule-item_center .program__time {
    color: #ffffff;
    font-size: 24px;
    font-weight: 700;
  }
  .program__schedule-item_center .program__name {
    text-align: center;
    width: calc( 100% - 140px);
  }
}

@media screen and (max-width: 920px) {
  .program {
    padding-bottom: 100px;
  }
}

@media screen and (max-width: 520px) {
  .program {
    padding-bottom: 80px;
  }
  .program__time + .program__name, .program__time + .program__content {
    margin-left: 0px;
    margin-top: 15px;
  }
  .program__name {
    width: 100%;
    text-align: center;
    font-size: 16px;
  }
  .program__time {
    font-size: 18px;
  }
  .program__schedule-item {
    padding: 15px 10px;
    flex-direction: column;
  }
  .program__schedule-item_center .program__time + .program__name {
    margin-left: 0 !important;
  }
  .program__schedule-item_center .program__name, .program__schedule-item_center .program__time {
    font-size: 18px;
  }
  .program__ordered-list li {
    padding-left: 0;
    font-size: 16px;
  }
}

@media screen and (max-width: 350px) {
  .program__schedule-item_center .program__time + .program__name {
    margin-left: 0 !important;
  }
  .program__schedule-item_center .program__name, .program__schedule-item_center .program__time {
    font-size: 16px;
  }
}
