@import "./varaibles";

.expert-item {
    display: block;
    width: 100%;
    overflow: hidden;
    &__img {
        width: 100%;
        height: 370px;
        object-fit: cover;
        margin-bottom: 20px;
        filter: grayscale(1);
        @include transition(filter); }
    &__name {
        display: block;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 21px;
        margin-bottom: 7px; }
    &__position {
        font-size: 18px;
        font-weight: 500;
        color: #ffffff;
        display: block; }
    &__subposition {
        font-size: 16px;
        color: $text-color-grey; }
    &:hover {
        .expert-item {
            &__img {
                filter: grayscale(0); } } } }

@media screen and ( max-width: 470px ) {
    .expert-item {
        &__img {
            height: 300px; }
        &__name {
            font-size: 18px; } } }
