@import "./varaibles";

.reaction {
    $dimention: 30px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    padding: {
        left: 35px;
        right: 35px; }
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style-type: none;
        @include transition(all);
        position: relative; }
    &__item {
        display: inline-block;
        height: $dimention;
        & + & {
            margin-left: 30px; }
        &:nth-child(1) .reaction__trigger {
            background-image: url(../img/like-1.png); }
        &:nth-child(2) .reaction__trigger {
            background-image: url(../img/like-2.png); }
        &:nth-child(3) .reaction__trigger {
            background-image: url(../img/like-3.png); }
        &:nth-child(4) .reaction__trigger {
            background-image: url(../img/like-4.png); }
        &:nth-child(5) .reaction__trigger {
            background-image: url(../img/like-5.png); } }
    &__trigger {
        display: inline-block;
        cursor: pointer;
        height: $dimention;
        width: $dimention;
        @include transition(transform);
        background-image: url(../img/like-1.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        overflow: visible !important;
        &_spirit {
            cursor: default;
            pointer-events: none;
            z-index: 10;
            position: absolute;
            top: 0px;
            right: 0px;
            opacity: .4;
            transition: all .1s linear 0s; } } }

@media screen and ( max-width: 520px ) {
    .reaction {
        $dimention: 25px;
        padding: {
            left: 0px;
            right: 0px; }
        &__item {
            height: $dimention;
            & + & {
                margin-left: 20px; } } } }
