.footer {
  flex-grow: 0;
  padding-bottom: 50px;
}

.footer__wrap {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  border-top: 2px solid #ffffff;
  padding-top: 35px;
}

.footer__col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 250px;
}

.footer__col + .footer__col {
  margin-left: 70px;
}

.footer__col:nth-of-type(2) {
  margin-left: auto;
}

.footer__logos .logo + .footer__logos-list {
  margin-left: 50px;
}

.footer__logos {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: auto;
}

.footer__logos-list {
  display: flex;
  align-items: flex-end;
  list-style-type: none;
}

.footer__logos-item + .footer__logos-item {
  margin-left: 30px;
}

.footer__logos-link {
  display: inline-block;
  height: 40px;
  width: 40px;
}

.footer__logos-link img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.footer__contacts {
  list-style-type: none;
  margin-bottom: 45px;
}

.footer__item + .footer__item {
  margin-top: 3px;
}

.footer__link {
  font-weight: 700;
  font-size: 18px;
  white-space: nowrap;
}

.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer__copyright {
  font-size: 12px;
  font-weight: 500;
  color: #898989;
  display: inline-block;
  margin-top: auto;
}

.footer__copyright:any-link:hover {
  text-decoration: underline;
}

.footer__copyright + .footer__copyright {
  margin-left: 50px;
}

@media screen and (max-width: 968px) {
  .footer__wrap {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 10px;
  }
  .footer__col {
    order: 2;
  }
  .footer__col + .footer__col {
    order: 1;
    margin-left: 0;
    margin-bottom: 20px;
  }
  .footer__col:nth-of-type(2) {
    margin-left: 0;
  }
  .footer__logos {
    order: 0;
    align-items: center;
  }
  .footer__logos-list {
    margin: 25px 0 25px 25px !important;
  }
  .footer__logos-link {
    height: 35px;
    width: 35px;
  }
  .footer__link {
    font-size: 16px;
  }
  .footer__bottom {
    flex-direction: column-reverse;
    align-items: flex-start;
    margin-top: 30px;
  }
  .footer__contacts {
    margin-bottom: 20px;
  }
  .footer__copyright + .footer__copyright {
    margin-left: 0px;
    margin-top: 20px;
  }
}

@media screen and (max-width: 350px) {
  .footer__logos-link {
    height: 30px;
    width: 30px;
  }
  .footer__logos-item + .footer__logos-item {
    margin-left: 15px;
  }
}
