.send-message {
  margin-top: 20px;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 35px;
  border-top: 2px solid #898989;
}

.send-message__input {
  flex-grow: 1;
  display: block;
  border: none;
  background-color: transparent;
  font-weight: 700;
  font-size: 21px;
  color: #ffffff;
  outline: none;
  resize: none;
}

.send-message__submit {
  padding: 23px 30px;
  font-size: 21px;
  font-weight: 900;
  flex-shrink: 0;
  text-transform: uppercase;
}

@media screen and (max-width: 588px) {
  .send-message {
    flex-direction: column;
    align-items: center;
    padding-top: 15px;
  }
  .send-message__input {
    font-size: 18px;
    font-weight: 500;
    height: 2em;
  }
  .send-message__submit {
    width: 100%;
    max-width: 320px;
    margin-top: 40px;
    padding: 21px;
  }
}
