.reaction {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  padding-left: 35px;
  padding-right: 35px;
}

.reaction__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;
  transition: all 0.3s ease-out 0s;
  position: relative;
}

.reaction__item {
  display: inline-block;
  height: 30px;
}

.reaction__item + .reaction__item {
  margin-left: 30px;
}

.reaction__item:nth-child(1) .reaction__trigger {
  background-image: url(../img/like-1.png);
}

.reaction__item:nth-child(2) .reaction__trigger {
  background-image: url(../img/like-2.png);
}

.reaction__item:nth-child(3) .reaction__trigger {
  background-image: url(../img/like-3.png);
}

.reaction__item:nth-child(4) .reaction__trigger {
  background-image: url(../img/like-4.png);
}

.reaction__item:nth-child(5) .reaction__trigger {
  background-image: url(../img/like-5.png);
}

.reaction__trigger {
  display: inline-block;
  cursor: pointer;
  height: 30px;
  width: 30px;
  transition: transform 0.3s ease-out 0s;
  background-image: url(../img/like-1.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  overflow: visible !important;
}

.reaction__trigger_spirit {
  cursor: default;
  pointer-events: none;
  z-index: 10;
  position: absolute;
  top: 0px;
  right: 0px;
  opacity: .4;
  transition: all .1s linear 0s;
}

@media screen and (max-width: 520px) {
  .reaction {
    padding-left: 0px;
    padding-right: 0px;
  }
  .reaction__item {
    height: 25px;
  }
  .reaction__item + .reaction__item {
    margin-left: 20px;
  }
}
