.expert-item {
  display: block;
  width: 100%;
  overflow: hidden;
}

.expert-item__img {
  width: 100%;
  height: 370px;
  object-fit: cover;
  margin-bottom: 20px;
  filter: grayscale(1);
  transition: filter 0.3s ease-out 0s;
}

.expert-item__name {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 21px;
  margin-bottom: 7px;
}

.expert-item__position {
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
  display: block;
}

.expert-item__subposition {
  font-size: 16px;
  color: #898989;
}

.expert-item:hover .expert-item__img {
  filter: grayscale(0);
}

@media screen and (max-width: 470px) {
  .expert-item__img {
    height: 300px;
  }
  .expert-item__name {
    font-size: 18px;
  }
}
