.dialog {
  padding-left: 70px;
  flex-grow: 1;
}

.dialog__messages {
  height: 380px;
  overflow-y: auto;
  margin-left: 25px;
  padding-right: 65px;
  list-style-type: none;
}

.dialog__messages::-webkit-scrollbar {
  width: 10px;
}

.dialog__messages::-webkit-scrollbar-thumb {
  border: none;
  background-color: #545454;
}

.dialog__messages::-webkit-scrollbar-track {
  background-color: #282828;
}

.dialog__message {
  margin-top: 25px;
}

@media screen and (max-width: 920px) {
  .dialog {
    padding-left: 0px;
  }
  .dialog__messages {
    margin-left: 0px;
    padding-right: 0px;
  }
  .dialog__message:first-child {
    margin-top: 0px;
  }
}
