@import "./varaibles";

.dialog {
    padding-left: 70px;
    flex-grow: 1;
    &__messages {
        height: 380px;
        overflow-y: auto;
        margin-left: 25px;
        padding-right: 65px;
        list-style-type: none;
        &::-webkit-scrollbar {
            width: 10px;
            &-thumb {
                border: none;
                background-color: #545454; }
            &-track {
                background-color: #282828; } } }
    &__message {
        margin-top: 25px; } }

@media screen and ( max-width: 920px ) {
    .dialog {
        padding-left: 0px;
        &__messages {
            margin-left: 0px;
            padding-right: 0px; }
        &__message {
            &:first-child {
                margin-top: 0px; } } } }
