.pulse-animate {
  position: relative;
}

.pulse-animate::before {
  content: '';
  height: 4px;
  width: 4px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(240, 53, 113, 0.8);
  box-shadow: 0 0 2px 2px rgba(240, 53, 113, 0.8);
  border-radius: 50%;
  position: absolute;
  z-index: 0;
  display: inline-block;
  transition: all 0.3s ease-out 0s;
}

.pulse-animate_animate::before {
  animation: pulse-animation .7s ease-out 0s 1;
}
