@import "./varaibles";

.header {
    position: fixed;
    z-index: 5;
    width: 100%;
    left: 0;
    top: 0;
    padding: 30px 0px;
    @include transition(all);
    flex-grow: 0;
    &.fixed {
        padding: 15px 0px;
        background-color: rgba(0, 0, 0, .85);
        .message-button {
            transform: translate(0, 0) rotate(45deg);
            height: 40px;
            width: 40px; } }
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: flex-end; } }

@media screen and ( max-width: 1140px ) {
    .header {
        background-color: rgba(0, 0, 0, .9);
        &__wrap {
            align-items: center; }
        &.fixed {
            background-color: rgba(0, 0, 0, .9); }
        .burger {
            display: inline-block !important; }
        .nav {
            display: none; } } }

@media screen and ( max-width: 720px ) {
    .header {
        padding: 15px 0px;
        &.fixed {
            padding: 10px 0px; } } }

