.nav {
  width: 100%;
  max-width: 810px;
}

.nav__list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  font-weight: bold;
  font-size: 18px;
  display: inline-block;
  position: relative;
}

.nav__link::after {
  content: '';
  display: inline-block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 2px;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease-out 0s;
}

.nav__link:hover::after {
  transform: scaleX(0.5);
}

.nav_header {
  margin: 0px 50px;
}

.nav_menu .nav__list {
  flex-direction: column;
  align-items: flex-start;
}

.nav_menu .nav__item + .nav__item {
  margin-top: 10px;
}

@media screen and (max-width: 920px) {
  .nav__link {
    padding: 5px 0px;
  }
  .nav_header {
    display: none;
  }
}
