@import "./varaibles";

.footer {
    flex-grow: 0;
    padding-bottom: 50px;
    &__wrap {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        border-top: 2px solid #ffffff;
        padding-top: 35px; }
    &__col {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 250px;
        & + & {
            margin-left: 70px; }
        &:nth-of-type(2) {
            margin-left: auto; } }
    &__logos .logo + &__logos-list {
        margin-left: 50px; }
    &__logos {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: auto;
        &-list {
            display: flex;
            align-items: flex-end;
            list-style-type: none; }
        &-item {
            & + & {
                margin-left: 30px; } }
        &-link {
            display: inline-block;
            height: 40px;
            width: 40px;
            img {
                height: 100%;
                width: 100%;
                object-fit: cover; } } }
    &__contacts {
        list-style-type: none;
        margin-bottom: 45px; }
    &__item {
        & + & {
            margin-top: 3px; } }
    &__link {
        font-weight: 700;
        font-size: 18px;
        white-space: nowrap; }
    &__bottom {
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__copyright {
        font-size: 12px;
        font-weight: 500;
        color: $text-color-grey;
        display: inline-block;
        margin-top: auto;
        &:any-link {
            &:hover {
                text-decoration: underline; } }
        & + & {
            margin-left: 50px; } } }

@media screen and ( max-width: 968px ) {
    .footer {
        &__wrap {
            flex-direction: column;
            align-items: flex-start;
            padding-top: 10px; }
        &__col {
            order: 2;
            & + & {
                order: 1;
                margin-left: 0;
                margin-bottom: 20px; }
            &:nth-of-type(2) {
                margin-left: 0; } }
        &__logos {
            order: 0;
            align-items: center;
            &-list {
                margin: 25px 0 25px 25px !important; }
            &-link {
                height: 35px;
                width: 35px; } }
        &__link {
            font-size: 16px; }
        &__bottom {
            flex-direction: column-reverse;
            align-items: flex-start;
            margin-top: 30px; }
        &__contacts {
            margin-bottom: 20px; }
        &__copyright {
            & + & {
                margin-left: 0px;
                margin-top: 20px; } } } }

@media screen and ( max-width: 350px ) {
    .footer {
        &__logos {
            &-link {
                height: 30px;
                width: 30px; }
            &-item {
                & + & {
                    margin-left: 15px; } } } } }

