.message-button {
  flex-shrink: 0;
  height: 50px;
  width: 50px;
  background-color: #ff4136;
  flex-shrink: 0;
  position: absolute;
  right: -90px;
  bottom: 0;
  transform: translate(0, 50%) rotate(45deg);
  box-shadow: 0 0 14px 5px #ff4136;
  transition: all 0.3s ease-out 0s;
  overflow: visible;
}

.message-button_has-message::before {
  content: attr(data-message);
  position: absolute;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  font-size: 10px;
  z-index: 2;
  top: -8px;
  right: 11px;
  height: 12px;
  width: 12px;
  background-color: #000000;
  transform: translate(0, 0) rotate(-45deg);
}

.message-button__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  max-height: 60%;
  width: auto;
}

@media screen and (max-width: 1460px) {
  .message-button {
    position: relative;
    bottom: auto;
    right: auto;
    transform: translate(0, 0) rotate(45deg);
    margin-left: 30px;
  }
}

@media screen and (max-width: 1140px) {
  .message-button {
    margin-left: auto;
    margin-right: 20px;
    height: 35px !important;
    width: 35px !important;
  }
}
