@import "./varaibles";

.nav {
    width: 100%;
    max-width: 810px;
    &__list {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style-type: none; }
    &__item {
        display: inline-block; }
    &__link {
        font-weight: bold;
        font-size: 18px;
        display: inline-block;
        position: relative;
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: currentColor;
            transform: scaleX(0);
            transform-origin: left;
            @include transition(transform); }
        &:hover {
            &::after {
                transform: scaleX(0.5); } } }
    &_header {
        margin: 0px 50px; }
    &_menu {
        .nav {
            &__list {
                flex-direction: column;
                align-items: flex-start; }
            &__item {} }
        .nav__item + .nav__item {
            margin-top: 10px; } } }

@media screen and ( max-width: 920px ) {
    .nav {
        &__link {
            padding: 5px 0px; }
        &_header {
            display: none; } } }
