.button {
  display: inline-block;
  border-radius: 0px;
  text-decoration: none;
  text-align: center;
  appearance: none;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;
  outline: none;
  overflow: hidden;
}

.button_gradient-border {
  color: #ff0a36;
  border: 3px solid #ff0a36;
  border-image: linear-gradient(to right, #ff0a36, #ff6b3a) 1 40%;
}

.button_big, .button_small {
  text-transform: uppercase;
  font-size: 21px;
  font-weight: 900;
  width: 100%;
  padding: 27px;
}

.button_big {
  max-width: 470px;
}

.button_small {
  max-width: 370px;
}

.button_bg {
  color: #ffffff;
  background-image: url(../img/submit-btn-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 768px) {
  .button_big, .button_small {
    padding: 21px;
  }
}
