@import "./varaibles";

.chat {
    display: flex;
    align-items: stretch;
    position: relative;
    &__dialogs {
        flex-shrink: 0;
        max-width: 295px;
        width: 100%;
        padding-right: 30px;
        max-height: 510px;
        overflow-y: auto;
        list-style-type: none;
        border-right: 1px solid $text-color-grey; }
    &__dialog {
        height: 100%;
        &-item {
            width: 100%; }
        &-link {
            width: 100%;
            display: inline-block;
            padding: 20px 30px;
            font-size: 21px;
            color: $text-color-grey;
            @include transition(all);
            &:hover, &.active {
                background-color: #282828;
                font-weight: 700;
                color: #ffffff; } } }
    &__trigger {
        display: none;
        position: absolute;
        z-index: 4;
        left: 0;
        top: 0;
        height: 35px;
        width: 35px;
        border: 1px solid #fff;
        border-radius: 3px;
        @include transition(background-color);
        transform: translateY(-120%);
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #ffffff;
            @include transition(fill); }
        &:hover, &.active {
            background-color: #fff;
            svg {
                fill: $body-bg; } } } }

@media screen and ( max-width: 920px ) {
    .chat {
        &__trigger {
            display: inline-block; }
        &__dialogs {
            padding: 0px 10px;
            border: none;
            z-index: 2;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            max-height: 590px;
            max-width: none;
            width: 100%;
            display: block;
            background-color: rgba(0, 0, 0, .9);
            transform: translateX(-100%);
            opacity: 0;
            pointer-events: none;
            @include transition(all);
            &.active {
                transform: translateX(0%);
                opacity: 1;
                pointer-events: all; } }
        &__dialog {
            &-link {
                padding: 15px 20px; } } } }
