@import "./varaibles";

.logo {
    display: block;
    max-width: 170px;
    width: 100%;
    flex-shrink: 0;
    font-size: 17px;
    font-weight: 700;
    &__img {
        width: 100%;
        height: auto; }
    &_powered {
        margin-left: auto; }
    &_footer {
        margin-bottom: 45px; }
    &_nav {
        margin-top: auto; }
    & + .burger {
        margin-left: 20px; } }

@media screen and ( max-width: 1140px ) {
    .logo {
        &_powered {
            display: none; } } }

@media screen and ( max-width: 720px ) {
    .logo {
        max-width: 110px;
        font-size: 14px;
        &_footer {
            margin-bottom: 0px; } } }
